export default {
  data() {
    return {
      user:{
        mobile:'',
        password:'',
        again_psd:'',
        reg_code:''
      }
    };
  },
  methods:{
    formSubmit(){
      
      var url,data;
      data = this.user;
      url = "user/reg";
      
      /*输入框非空判断*/
      if(!(/^1[34578]\d{9}$/).test(data.mobile)){
        uni.showToast({
          title:"请输入有效的手机号码",
          icon:"none"
        })
        return false;
      }else if(!(/^[a-zA-Z0-9]{6,}$/).test(data.password)){
        uni.showToast({
          title:"密码格式最少需要字母*数字!",
          icon:"none"
        })
        return false;
      }else if(data.again_psd !== data.password){
        uni.showToast({
          title:"两次密码输入不一致!",
          icon:"none"
        })
        return false;
      }else if(!(/^[a-zA-Z0-9]{4}$/).test(data.reg_code)){
        uni.showToast({
          title:"邀请码输入有误!",
          icon:"none"
        })
        return false;
      } 
      this.$Request.post(url, data).then( res => {
        if(res && res.code === 200){
          uni.showToast({
            title:"注册成功",
            icon:"success"
          })
          /* 注册成功后页面跳转*/
          setTimeout(function() {
            uni.switchTab({
             url: "/pages/tabBar/onGoods/onGoods"
           })
          }, 500)
          
        }else{
          // 注册错误信息
          uni.showToast({
            title:res.msg,
            icon:"none"
          })
        }
      })
    }
  }
}
